.footer {
  width: 100%;
  border-top: 2px solid var(--color-main-dark);

  .container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
  }

}

.amount,
.social,
.social__share,
.social__networks {
  display: flex;
  // flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.amount {
  margin-right: 2rem;

  p {
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    font-weight: 700;
    text-transform: uppercase;
    color: var(--color-main-dark);
  }
}

.amoun__money {
  font-size: 2.4rem;
  margin-left: 2rem;
  color: var(--color-accent);
  transform: translate(0, -2px);

  &::after {
    content: "₽";
    display: inline-block;

  }
}

.amoun__total {
  font-size: 1.8rem;
  font-weight: 400;
  opacity: .5;
  margin-right: 2rem;
  transform: translate(0, -2px);

  &::before {
    content: "/";
    display: inline-block;
    padding-right: 1rem;
    padding-left: 1rem;
  }

  &::after {
    content: "₽";
    display: inline-block;

  }
}

.amount__participants {
  font-size: 2.4rem;
  padding-left: 2rem;
  color: var(--color-accent);
  transform: translate(0, -2px);
}

.social {

  button:not(:nth-child(3)) {
    margin-right: 1.6rem;
  }

  ._icon-share {
    font-size: 1.5rem;
    padding: 10px 12px;
  }
}

.social__networks {
  margin-right: 2rem;

  li:not(:last-of-type) {
    margin-right: .8rem;
  }

  .btn-social {
    color: var(--color-accent);
  }

  ._icon-vk {
    font-size: 1.1rem;
    padding: 10px;
    padding-top: 14px;
  }

  ._icon-facebook {
    font-size: 1.5rem;
    padding: 10px;
  }


  ._icon-instagram {
    font-size: 1.5rem;
    padding: 10px;
  }

  ._icon-youtube {
    font-size: 1.6rem;
    padding: 9px;
  }
}

.burger-footer>.social {
  display: none;
}

@media (max-width: 1246px) {
  .footer {

    .container {
      flex-direction: column;
    }


  }

  .amount {
    margin-bottom: 1rem;
  }

}


@media (max-width: 700px) {
  .social {
    display: none;
  }

  .burger-footer>.social {
    display: flex;
    flex-direction: column;
    align-items: start;
    padding: 1rem;
    padding-bottom: 3rem;
    transform: translate(0, 11rem);

    .btn-social {
      margin: 0;
      margin-bottom: 1.5rem;
    }

    .social__networks,
    .social__networks li:not(:last-of-type) {
      margin: 0;
    }

    .social__share {
      margin-bottom: 1.4rem;

      .btn-social {
        margin: 0;
        margin-right: 1rem;
      }

      ._icon-share {
        font-size: 1.6rem;
        padding: 11px 14px;
      }

      ._icon-youtube {
        margin: 0;
      }
    }

  }
}


@media (max-width: 598px) {

  .amount {
    margin: 0;
  }

  .amount p {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;

  }

  .amount p :not(:last-of-type) {
    margin-right: 5rem;
  }

  .amount span {
    display: block;
    white-space: nowrap;
    margin: 0;
    padding: 0;
  }

  .amoun__total {
    transform: translate(0, 0px);

    &::before {
      padding-left: 0rem;
    }
  }
}
